<template>
    <b-overlay class="w-100 h-100" spinner-variant="light" variant="primary" :show="loading">
        <div class="p-4 d-flex justify-content-center" style="background: #eee; min-height: 100%">
            <div class="w-100" style="max-width: 425px;">
                <b-select :options="entidadesOptions" v-model="scheduler.entidad" @change="cargarConfiguracionAPI">
                    <template #first>
                        <b-select-option disabled :value="null">--Seleccione Entidad--</b-select-option>
                    </template>
                </b-select>
                <transition name="fade">
                    <div v-if="scheduler.entidad" class="text-center">
                        <p class="mt-4 mb-2">Das en los que se ejecutar la validacin de egresos</p>
                        <div class="d-flex justify-content-center">
                            <template v-for="(dia, index) in diasOptions">
                                <b-button class="mr-1 p-0 rounded-circle text-center" 
                                    :key="index"
                                    style="width: 40px; height: 40px;"
                                    :variant="diaIsSelected(dia.value) ? 'primary' : 'outline-secondary'"
                                    @click="toggleDia(dia.value)"
                                >{{dia.text}}</b-button>
                            </template>
                        </div>
                        <p class="mt-4 mb-2">Horario de inicio</p>
                        <div class="d-flex justify-content-center mb-5">
                            <div class="d-flex p-2 align-items-center">
                                <b-button-group vertical>
                                    <b-button @click="scheduler.horaInicio++">
                                        <b-icon-chevron-compact-up></b-icon-chevron-compact-up>
                                    </b-button>
                                    <b-form-input class="bg-secondary text-light border-secondary rounded-0 text-center"
                                    style="font-size: 2rem; width: 70px" 
                                    v-model="scheduler.horaInicio" 
                                    lazy></b-form-input>
                                    <b-button @click="scheduler.horaInicio--">
                                        <b-icon-chevron-compact-down></b-icon-chevron-compact-down>
                                    </b-button>
                                </b-button-group>
                                <div class="p-2">
                                    <b-icon-dot class="d-block"></b-icon-dot>
                                    <b-icon-dot class="d-block"></b-icon-dot>
                                </div>
                                <b-button-group vertical>
                                    <b-button @click="scheduler.minutoInicio++">
                                        <b-icon-chevron-compact-up></b-icon-chevron-compact-up>
                                    </b-button>
                                    <b-form-input class="bg-secondary text-light border-secondary rounded-0 text-center"
                                    style="font-size: 2rem; width: 70px" 
                                    v-model="scheduler.minutoInicio"
                                    lazy></b-form-input>
                                    <b-button @click="scheduler.minutoInicio--">
                                        <b-icon-chevron-compact-down></b-icon-chevron-compact-down>
                                    </b-button>
                                </b-button-group>
                            </div>
                        </div>
                        <div class="d-flex justify-content-center">
                            <b-button variant="primary" @click="guardarConfiguracionAPI">Guardar Configuracin</b-button>
                        </div>
                    </div>
                </transition>
            </div>
        </div>
    </b-overlay>
</template>

<script>
import axios from 'axios'
import { RequestHelper } from '../util/utils';

export default {
    data() {
        return {
            scheduler: {
                entidad: null,
                horaInicio: 0,
                minutoInicio: 0,
                dias: []
            },
            entidadesOptions: [],
            diasOptions: [
                {value: 1, text: "Lu"},
                {value: 2, text: "Ma"},
                {value: 3, text: "Mi"},
                {value: 4, text: "Ju"},
                {value: 5, text: "Vi"},
                {value: 6, text: "Sa"},
                {value: 7, text: "Do"}
            ],
            loading: false
        }
    },
    inject: ['showLoginModal', 'errorHandling', 'createToast'],
    methods: {
        cargarEntidadesAPI() {
            this.loading = true;

            RequestHelper.get('/api/admin/entidades', {
                success: (data) => {
                    // Cargo las entidades jurdicas y despues las base
                    this.entidadesOptions = data.entidadesJuridicas.map((juridica) => {
                        return {
                            text: `${juridica.nombreFicticio} - Entidad Jurdica`,
                            value: juridica.id
                        }
                    });

                    data.entidadesJuridicas.forEach((juridica) => {
                        juridica.entidadesBase.forEach((base) => {
                            this.entidadesOptions.push({
                                text: `${base.nombreFicticio} - Entidad Base`,
                                value: base.id
                            });
                        })
                    })
                },
                notLoggedIn: () => {
                    this.showLoginModal(true);
                },
                forbidden: (error) => {
                    this.errorHandling(error);
                },
                error: (error) => {
                    this.errorHandling(error);
                },
                always: () => {
                    this.loading = false;
                }
            });
        },
        cargarConfiguracionAPI() {

            if(!this.scheduler.entidad)
                return;

            this.loading = true;

            RequestHelper.get(`/api/bandeja/configuracion/${this.scheduler.entidad}`, {
                success: (data) => {
                    this.scheduler.dias = data.schedulerInit.dias;
                    this.scheduler.horaInicio = data.schedulerInit.horaInicio;
                    this.scheduler.minutoInicio = data.schedulerInit.minutoInicio;

                    this.updateHoraYMinuto();
                },
                notLoggedIn: () => {
                    this.showLoginModal(true);
                },
                forbidden: (error) => {
                    this.errorHandling(error);
                },
                error: (error) => {
                    this.errorHandling(error);
                },
                always: () => {
                    this.loading = false;
                }
            });
        },
        guardarConfiguracionAPI() {
            this.loading = true;

            this.scheduler.organizacionId = this.scheduler.entidad;
            console.log(this.scheduler);

            RequestHelper.post('/api/admin/bandeja/configurar', this.scheduler, {
                success: (data) => {
                    this.createToast('Guardado exitoso', 'Se guardo la configuracin correctamente', 'success');
                    this.updateHoraYMinuto();
                },
                notLoggedIn: () => {
                    this.showLoginModal(false);
                },
                forbidden: (error) => {
                    this.errorHandling(error);
                },
                error: (error) => {
                    this.errorHandling(error);
                },
                always: () => {
                    this.loading = false;
                }
            });
        },
        toggleDia(dia) {
            if(this.diaIsSelected(dia)) {
                this.scheduler.dias.splice(this.getDiaIndex(dia), 1);
            } else {
                this.scheduler.dias.push(dia);
            }
        },
        diaIsSelected(dia) {
            return this.getDiaIndex(dia) != - 1;
        },
        getDiaIndex(dia) {
            return this.scheduler.dias.indexOf(dia);
        },
        updateHoraYMinuto() {
            this.scheduler.horaInicio = ('0' + this.scheduler.horaInicio).slice(-2);
            this.scheduler.minutoInicio = ('0' + this.scheduler.minutoInicio).slice(-2);
        },
        formatHora(hora) {
            if(isNaN(hora))
                this.scheduler.horaInicio = 0;

            if(hora > 23) {
                this.scheduler.horaInicio = 0
            } else if(hora < 0) {
                this.scheduler.horaInicio = 23
            }
        },
        formatMinuto(minuto) {
            if(isNaN(minuto))
                this.scheduler.minutoInicio = 0;

            if(minuto > 59) {
                this.scheduler.minutoInicio = 0;
                this.scheduler.horaInicio++;
            } else if(minuto < 0) {
                this.scheduler.minutoInicio = 59;
                this.scheduler.horaInicio--;
            }
        }
    },
    mounted() {
        this.cargarEntidadesAPI()
    },
    watch: {
        'scheduler.horaInicio'(hora) {
            this.formatHora(hora);
            this.updateHoraYMinuto();
        },
        'scheduler.minutoInicio'(minuto) {
            this.formatMinuto(minuto);
            this.updateHoraYMinuto();
        }
    }
}
</script>

